<template>
    <div class="wrap paddingBottom" ref="wrap">
        <div class="icon icon_back head_goback" @click="$router.go(-1)"></div>
        <section class="carousel">
            <div class="swiper-container">
                <div class="swiper-wrapper">
                    <div class="swiper-slide" v-for="(item, index) in goods.image_list" :key="index">
                        <img :src="item">
                    </div>
                </div>
                <div class="swiper-pagination"></div>
            </div>
        </section>
        <section class="title-view">
            <section class="title">
                <p class="name">{{ goods.goods_name }}</p>
            </section>
            <section class="price">
                <p class="price-count">{{ goods.shop_price }}</p>
                <p class="price-origin">{{ goods.shop_price }}</p>
            </section>
            <section class="spec-choose collapse-right" @click="showSpecMenu">
                <p class="has">已选</p>
                <p class="chooseInfo">{{ specSelectedStr }} x {{ num }}</p>
            </section>
        </section>
        <section class="tab-view">
            <div class="tab-container" ref='tabContainer'>
                <ul class="tabs" ref='tab'>
                    <li :class="{ active : (tabActive === 0) }" @click="scrollToConsult">商品咨询</li>
                    <li :class="{ active : (tabActive === 1) }" @click="scrollToDetail">商品详情</li>
                </ul>
            </div>
            <div class="consult" ref="consult">
                <div class="comment-list">
                    <div class="item">
                        <div class="user">
                            <div class="avatar">
                                <img src="http://stor.ihuipao.cn/@/avatar/96606/1174.jpg?v=6893">
                            </div>
                            <div class="name">
                                <p>陈城</p>
                                <p class="date">2017-03-14</p>
                            </div>
                        </div>
                        <div class="content">联系电话无人接，我的付款沒有成功，怎麼辦</div>
                        <div class="reply">
                            <ul>
                                <li>
                                    <div class="avatar">
                                        <img src="http://r0.ihuipao.cn/img/huipao.png">
                                    </div>
                                    <div class="textbox">
                                        <span class="tit">官方回复：</span>
                                        <span>我想你，想打定话给你，想花胆信给你，想呛首歌给你，想做换给你，想请你喝咖灰，想跟你一起吹吹轰，想跟你一起看熏熏···想你，想到无花付息，想到牛眼内，想到昏古七。但，又想了想还是放哈了定话，我，要控计我计己。 感谢您对汇跑的支持。</span>
                                    </div>
                                </li>
                            </ul>
                        </div>
                    </div>
                </div>
                <div class="more collapse-right">
                    <span class="left">用户留言(1)</span>
                    <span class="right">查看全部留言</span>
                </div>
            </div>
            <div id="detail" class="detail" ref="detail"></div>
        </section>
        <section ref="specMenu" v-show="specMenuIsShow" class="spec-menu" @click="closeSpecMenu">
            <div class="panel" :class="{enter : panelEnter}">
                <div ref="close" class="close" @click="closeSpecMenu"></div>
                <div class="pro-info">
                    <div class="pro-thumbnail">
                        <img :src="specSelectedImg">
                    </div>
                    <div class="pro-desc">
                        <p class="price">{{ sumPrice }}</p>
                        <p class="desc">{{ specSelectedStr }}</p>
                    </div>
                </div>
                <div class="spec-container">
                    <div class="spec-group" v-for="(spec, index) in goods.filter_spec_list" :key="index">
                        <div class="spec-title">{{ spec.name }}</div>
                        <div class="spec-row">
                            <div v-for="(item, idx) in spec.items" :key="idx" class="spec-item" :class="{ selected : (idx === specIdxSelected[index]) }" @click="selectSpec(index, idx)">
                                <div v-if="item.src" class="spec-img">
                                    <img :src="item.src">
                                </div>
                                <p>{{ item.value }}</p>
                            </div>
                        </div>
                    </div>
                    <div class="spec-num">
                        <p class="spec-title">购买数量</p>
                        <div class="input-num">
                            <div class="sub" @click="subNum">-</div>
                            <div class="input">{{ num }}</div>
                            <div class="add" @click="addNum">+</div>
                        </div>
                    </div>
                </div>
            </div>
        </section>
        <div class="foot-buy">
            <div class="home" @click="$router.push({ path: 'home' })">
                <div class="icon icon_home_black"></div>
                <p>首页</p>
            </div>
            <div class="cart" @click="$router.push({ path: 'cart' })">
                <div class="icon icon_cart_black"></div>
                <p>购物车</p>
            </div>
            <div class="addCart" @click="addToCart">加入购物车</div>
            <div class="buyNow">立即购买</div>
        </div>
    </div>
</template>

<script>
import { mapMutations } from 'vuex'
import { getProductData } from 'src/service/getData'
import 'src/plugins/swiper.min.js'
import 'src/style/swiper.min.css'
import BScroll from 'better-scroll'

export default {
    data() {
        return {
            id: '',
            tabTop: 0,
            scrollWatch: true,
            tabActive: -1,
            specMenuIsShow: false,
            panelEnter: false,
            goods: {},
            specIdxSelected: [],
            specSelected: [],
            specSelectedStr: '',
            specSelectedImg: '',
            specSelectedId: '',
            num: 1,
            sumPrice: 0,
        }
    },
    beforeRouteEnter (to, from, next) {
        next(vm => {
            vm.id = vm.$route.query.id;
            getProductData(vm.id).then(res => {
                console.log(res);
                vm.goods = res.goods;
                vm.specSelectedImg = res.goods.image_list[0];
                vm.sumPrice = res.goods.price;
                for(var i in res.goods.filter_spec_list) {
                    var temp = {
                        name: res.goods.filter_spec_list[i].name,
                        item_id: res.goods.filter_spec_list[i].items[0].item_id,
                        value: res.goods.filter_spec_list[i].items[0].value,
                        src: res.goods.filter_spec_list[i].items[0].src,
                    }
                    vm.specIdxSelected.push(0);
                    vm.specSelected.push(temp);
                }
                setTimeout(function() {
                    vm.initSwiper();
                    vm.$refs.detail.innerHTML = vm.goods.goods_content;
                }, 300);
            });
        })
    },
    created() {
        const _self = this;
        _self.SHOW_HEADTOP(false);
        // console.log(_self.$refs.tab);
        _self.SHOW_FOOTNAV(false);
    },
    mounted() {
        const _self = this;
        // console.log(_self.$refs.tabContainer);
        // console.log(_self.$refs.tab);
        setTimeout(function() {
            _self.$refs.tabContainer.style.height = _self.$refs.tab.clientHeight + 'px';
            _self.tabStick();
        },300);
    },
    destroyed () {
        this.scrollWatch = false;
    },
    watch: {
        specIdxSelected: function(value) {
            this.updateSpecSelectedStr(value);
            this.updateSpecSelectedImg(value);
            this.updateSpecSelectedId(value);
        },
        // num: function(value) {
        //     this.sumPrice = this.goods.price * value;
        // }
    },
    methods: {
        ...mapMutations(['LOADING','SHOW_HEADTOP','SHOW_HEADTOP_BACK','SHOW_HEADTOP_SEARCH','SHOW_FOOTNAV','ADD_CART']),
        initSwiper() {
            new Swiper('.swiper-container', {
                // loop: true,
                autoplay: 5000,
                pagination: '.swiper-pagination',
            });
        },
        tabStick() {     //tab标签页面滚动粘滞
            const _self = this;
            _self.tabTop = _self.$refs.tab.offsetTop;
            _self.$nextTick(function() {
                window.addEventListener('scroll', function() {
                    if(_self.scrollWatch) {
                        var top = document.getElementsByTagName('body')[0].scrollTop;
                        if(top >= _self.$refs.tabContainer.offsetTop) {
                            _self.$refs.tab.style.position = 'fixed';
                            _self.$refs.tab.style.top = 0;
                            _self.$refs.tab.style.left = 0;
                            _self.tabActive = 0;   //设置标签active状态
                            if(top >= (_self.$refs.detail.offsetTop - _self.$refs.tab.clientHeight)) {
                                _self.tabActive = 1;
                            }
                        } else {
                            _self.tabActive = -1;
                            _self.$refs.tab.style.position = 'relative';
                            _self.tabTop = _self.$refs.tab.offsetTop;
                        }
                    }
                }, true);
            });
        },
        scrollToConsult() {
            window.scrollTo(0, this.$refs.tabContainer.offsetTop);
        },
        scrollToDetail() {
            window.scrollTo(0, this.$refs.detail.offsetTop - this.$refs.tab.clientHeight);
        },
        showSpecMenu() {
            const _self = this;
            _self.specMenuIsShow = true;
            setTimeout(function() {
                _self.panelEnter = true;
            }, 100);
        },
        closeSpecMenu(e) {
            const _self = this;
            if(!e || e.target === _self.$refs.specMenu || e.target === _self.$refs.close) {
                _self.panelEnter = false;
                setTimeout(function() {
                    _self.specMenuIsShow = false;
                }, 300);
            } else {
                return false;
            }
        },
        selectSpec(index, idx) {
            this.specSelected[index] = this.goods.filter_spec_list[index].items[idx];
            this.$set(this.specIdxSelected, index, idx);  //调用Vue set方法，检测数组变动才能更新视图
        },
        updateSpecSelectedStr(value) {   //更新已选择属性显示文本
            this.specSelectedStr = '';
            for(var i in value) {
                this.specSelectedStr += this.specSelected[i].value + ' ';
            }
        },
        updateSpecSelectedImg(value) {   //更新预览图
            for(var i in value) {
                if(this.specSelected[i].src) {
                    this.specSelectedImg = this.specSelected[i].src;
                }
            }
        },
        updateSpecSelectedId(value) {   //更新已选择属性id
            this.specSelectedId = '';
            for(var i in value) {
                this.specSelectedId += this.specSelected[i].item_id + '-';
            }
            var a = this.specSelectedId;
            this.specSelectedId = a.substring(0, a.length - 1);
        },
        addNum() {
            if(this.num < Number(this.goods.store_count)) {
                this.num ++;
            }
        },
        subNum() {
            if(this.num > 1) {
                this.num --;
            }
        },
        addToCart() {
            const _self = this;
            _self.LOADING(true);
            setTimeout(function() {
                _self.ADD_CART({
                    id: _self.goods.goods_id + '-' + _self.specSelectedId,
                    goods_id: _self.goods.goods_id,
                    spec_id: _self.specSelectedId,
                    goods_name: _self.goods.goods_name,
                    store_count: _self.goods.store_count,
                    price: _self.goods.price,
                    img: _self.specSelectedImg,
                    spec: _self.specSelectedStr,
                    num: _self.num
                });
                _self.LOADING(false);
                _self.closeSpecMenu();
            }, 1000);

        }
    }
}
</script>

<style lang="scss" scoped>
@import '../../style/icon';
@import '../../style/component';

@mixin paddingLR {
    padding-left: .8rem;
    padding-right: .8rem;
}

@mixin paddingTB($top, $bottom) {
    padding-top: $top;
    padding-bottom: $bottom;
}

.wrap {
    height: auto;
}

.head_goback {
    border-radius: 100%;
    background-color: rgba(0, 0, 0, .3);
    background: url('data:image/png;base64,') no-repeat;
    background-size: 100%;
    background-position: center;
    line-height: 1rem;
}

.title-view {
    background-color: #fff;
    @include paddingLR;
    .title, .price, .spec-choose {
        @include paddingTB(.4rem, .4rem);
    }
    .title {
        .name {
            font-size: .7rem;
            font-weight: 600;
        }
    }
    .price {
        border-bottom: 1px solid #f4f4f4;
        .price-count, .price-origin {
            position: relative;
            display: inline-block;
            vertical-align: middle;
        }
        .price-count {
            margin-left: .5rem;
            font-size: 1rem;
            color: $red;
            &:before {
                content: '¥';
                position: absolute;
                font-size: .6rem;
                top: 4px;
                left: -.4rem;
            }
        }
        .price-origin {
            color: #9e9e9e;
            &:before {
                content: '';
                display: block;
                position: absolute;
                top: 50%;
                left: -5%;
                width: 115%;
                height: 2px;
                background: #9e9e9e;
            }
        }
    }
    .spec-choose {
        .has {
            font-size: .4rem;
            color: #9a9a9a;
        }
        .chooseInfo {
            font-size: .5rem;
            line-height: 1.2rem;
        }
    }
}

.tab-view {
    background-color: #fff;
    margin-top: 10px;
    padding-bottom: 40px;
    .tabs {
        position: relative;
        @include wh(100%, 2rem);
        background-color: #fff;
        border-bottom: 1px solid #eee;
        @include fmidhoz;
        z-index: 1000;
        li {
            font-size: .56rem;
            width: 50%;
            height: .8rem;
            line-height: .8rem;
            text-align: center;
            border-left: 1px solid #e4e4e4;
            &:nth-child(1) {
                border-left: none;
            }
            &.active {
                color: $red;
            }
        }
    }
    .consult {
        @include paddingLR;
        @include paddingTB(1rem, 0rem);
        .more {
            height: 2rem;
            padding-right: 1rem;
            border-bottom: 1px solid #eee;
            @include fbethoz;
            .left {
                font-size: .64rem;
                font-weight: 400;
            }
            .right {
                font-size: .4rem;
                font-weight: 300;
            }
        }
        .comment-list {
            .item {
                margin: 20px 0;
                .user {
                    @include flefthoz;
                    .avatar {
                        @include wh(1.8rem, 1.8rem);
                        border-radius: 50%;
                        overflow: hidden;
                        img {
                            width: 100%;
                        }
                    }
                    .name {
                        box-flex: 1;
                        width: 60%;
                        margin-left: 6%;
                    }
                    .more {
                        font-size: .5rem;
                    }
                }
                .content {
                    margin-top: 10px;
                    margin-bottom: 10px;
                }
                .reply {
                    padding: .4rem;
                    background-color: #eee;
                    ul {
                        li {
                            position: relative;
                            .avatar {
                                position: absolute;
                                left: 0;
                                top: 0;
                                @include wh(1.2rem, 1.2rem);
                                overflow: hidden; 
                            }
                            .textbox {
                                font-size: .5rem;
                                .tit {
                                    color: $red;
                                }
                                text-indent: 1.6rem;
                            }
                        }
                    }
                }
            }
        }
    }
    .detail {
        @include paddingLR;
        @include paddingTB(1rem, 0rem);
    }
}

.spec-menu {
    position: fixed;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    width: 100%;
    background-color: rgba(0, 0, 0, 0.3);
    z-index: 1000;
    .panel {
        @include paddingLR;
        width: 100%;
        position: absolute;
        bottom: 0;
        background-color: #fff;
        padding-bottom: 2.25rem;
        transform: translate3d(0, 100%, 0)!important;
        transition: transform .3s ease;
        // transition-delay: .3s;
        &.enter {
            transform: translate3d(0, 0, 0)!important;
        }
        .close {
            position: absolute;
            top: .4rem;
            right: .4rem;
            @include wh(1.4rem, 1.4rem);
            background: url('../../images/close.png') no-repeat;
            background-size: 60%;
            background-position: center;
        }
        .pro-info {
            min-height: 3.8rem;
            @include flefthoz;
            .pro-thumbnail {
                @include wh(4rem, 4rem);
                margin-top: -1.4rem;
                padding: .1rem;
                background-color: #fff;
                border-radius: 2px;
                img {
                    width: 100%;
                }
            }
            .pro-desc {
                width: 9rem;
                margin-left: .3rem;
                .price {
                    position: relative;
                    font-size: 1rem;
                    margin-left: .5rem;
                    color: $red;
                    &:before {
                        content: '¥';
                        position: absolute;
                        font-size: .6rem;
                        top: 4px;
                        left: -.4rem;
                    }
                }
                .desc {
                    width: 100%;
                    overflow: hidden;
                    text-overflow: ellipsis;
                    font-size: .5rem;
                }
            }
        }
        .spec-container {
            max-height: 12rem;
            overflow-y: auto;
            border-top: 1px solid #eee;
            padding-top: .6rem;
            .spec-title {
                font-size: .6rem;
            }
            .spec-group {
                margin-bottom: .6rem;
                .spec-row {
                    @include flefthoz;
                    border-bottom: 1px solid #eee;
                    padding-bottom: .6rem;
                    .spec-item {
                        font-size: .5rem;
                        border-radius: 2px;
                        padding: .2rem .4rem;
                        margin-right: .3rem;
                        margin-top: .3rem;
                        @include flefthoz;
                        &.selected {
                            border: 1px solid $red;
                        }
                        .spec-img {
                            margin-right: .3rem;
                            @include wh(.9rem, .9rem);
                        }
                    }
                }
            }
            .spec-num {
                @include fbethoz;
                margin-bottom: .6rem;
                .input-num {
                    @include fmidhoz;
                    border: 1px solid #eee;
                    color: #333;
                    .sub, .add {
                        font-size: 1.2rem;
                        font-weight: 300;
                        color: #c9c9c9;
                    }
                    .sub, .input, .add,  {
                        @include wh(1.6rem, 1.6rem);
                        text-align: center;
                        line-height: 1.6rem;
                    }
                    .input {
                        border-left: 1px solid #eee;
                        border-right: 1px solid #eee;
                    }
                }
            }
        }
    }
}

.foot-buy {
    position: fixed;
    bottom: 0;
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: stretch;
    background-color: #fff;
    border-top: 1px solid #eee;
    z-index: 1090;
    .home, .cart {
        box-sizing: border-box;
        padding: .2rem 1rem;
        @include fmidver;
        align-content: space-around;
        p {
            color: #8a8a8a;
            font-size: .5rem;
        }
    }
    .home {
        border-right: 1px solid #eee;
    }
    .addCart, .buyNow {
        text-align: center;
        line-height: 2.25rem;
        font-size: .7rem;
        font-weight: 500;
        color: #fff;
        flex-grow: 1;
    }
    .addCart {
        background-color: #f90;
    }
    .buyNow {
        background-color: $red;
    }
}

</style>


